import React from 'react';
import {List,ListItem} from "@material-ui/core/";
function Todo(props)
{
    return (
        <ol>
            {props.allData.map((value, index) =>{
                return (
                    <List key={ index }>
                        <ListItem className="h5">Note</ListItem>
                        <ListItem>
                       
                         {index+1}. {value }
                   
                        </ListItem>
                   
                    </List>
            )})} 
       
     </ol>
    );
}

export default Todo;
